import React, { Component } from "react";
import homeTeamData from "../data/homeTeamData";
import roadTeamData from "../data/roadTeamData";

class MainContent extends Component {
  constructor() {
    super();
    this.state = {
      homeData: homeTeamData,
      roadData: roadTeamData,
      home: 3,
      road: 26,
      weatherTemp: "",
    };
    this.handleUpHome = this.handleUpHome.bind(this);
    this.handleDownHome = this.handleDownHome.bind(this);
    this.handleUpRoad = this.handleUpRoad.bind(this);
    this.handleDownRoad = this.handleDownRoad.bind(this);
  }

  handleUpHome() {
    this.setState((prevState) => {
      if (prevState.home === 0) {
        return { home: 30 };
      } else return { home: prevState.home - 1, weatherTemp: "" };
    });

    const key = "002673e820dd167f2d42f3845e8e38c2";
    const forcast = this.state.homeData[this.state.home].forcast;
    const api =
      "https://api.openweathermap.org/data/2.5/forecast?id=" +
      forcast +
      "&units=imperial&APPID=" +
      key;

    fetch(api)
      .then((response) => response.json())
      .then((data) => {
        let temp = Math.round(data.list[0].main.temp);
        this.setState({ weatherTemp: temp });
        console.log(data);
      });
  }

  handleDownHome() {
    this.setState((prevState) => {
      if (prevState.home === 30) {
        return { home: 0 };
      } else return { home: prevState.home + 1, weatherTemp: "" };
    });

    const key = "002673e820dd167f2d42f3845e8e38c2";
    const forcast = this.state.homeData[this.state.home].forcast;
    const api =
      "https://api.openweathermap.org/data/2.5/forecast?id=" +
      forcast +
      "&units=imperial&APPID=" +
      key;

    fetch(api)
      .then((response) => response.json())
      .then((data) => {
        let temp = Math.round(data.list[0].main.temp);
        this.setState({ weatherTemp: temp });
        console.log(data);
      });
  }

  handleUpRoad() {
    this.setState((prevState) => {
      if (prevState.road === 0) {
        return { road: 30 };
      } else return { road: prevState.road - 1 };
    });
  }

  handleDownRoad() {
    this.setState((prevState) => {
      if (prevState.road === 30) {
        return { road: 0 };
      } else return { road: prevState.road + 1 };
    });
  }

  componentDidMount() {
    const key = "002673e820dd167f2d42f3845e8e38c2";
    const forcast = this.state.homeData[this.state.home].forcast;
    const api =
      "https://api.openweathermap.org/data/2.5/forecast?id=" +
      forcast +
      "&units=imperial&APPID=" +
      key;

    fetch(api)
      .then((response) => response.json())
      .then((data) => {
        let temp = Math.round(data.list[0].main.temp);
        this.setState({ weatherTemp: temp });
        console.log(data);
      });
  }

  render() {
    function date() {
      let newDate = new Date();
      let month = newDate.getMonth();
      const calendar = new Array(12);
      calendar[0] = "January";
      calendar[1] = "February";
      calendar[2] = "March";
      calendar[3] = "April";
      calendar[4] = "May";
      calendar[5] = "June";
      calendar[6] = "July";
      calendar[7] = "August";
      calendar[8] = "September";
      calendar[9] = "October";
      calendar[10] = "November";
      calendar[11] = "December";
      let day = newDate.getDay();
      const weekday = new Array(7);
      weekday[0] = "Sunday";
      weekday[1] = "Monday";
      weekday[2] = "Tuesday";
      weekday[3] = "Wednesday";
      weekday[4] = "Thursday";
      weekday[5] = "Friday";
      weekday[6] = "Saturday";

      return (
        weekday[day].toUpperCase() +
        ", " +
        calendar[month].toUpperCase() +
        " " +
        newDate.getDate() +
        "th"
      );
    }

    return (
      <section>
        <div className="matchup">
          <div
            className="home"
            style={{
              backgroundImage:
                "url(" + this.state.homeData[this.state.home].img + ")",
            }}
          >
            <h1>home</h1>
            <div className="home-team">
              <h2>{this.state.homeData[this.state.home].city}</h2>
              <h3>{this.state.homeData[this.state.home].name}</h3>
            </div>
          </div>
          <div className="middle">
            <div className="weather">
              <div className="temp">{this.state.weatherTemp}Â°F</div>
              <div className="location">
                <i className="fas fa-map-marker-alt"></i>
                {this.state.homeData[this.state.home].weather}
              </div>
            </div>
            <div className="todays-matchup">
              <h1>todays matchup</h1>
              <h2>{date()}</h2>
            </div>
            <div className="vs">
              <div className="options">
                <i
                  className="fas fa-chevron-up"
                  onClick={this.handleUpHome}
                ></i>
                <i
                  className="fas fa-chevron-down"
                  onClick={this.handleDownHome}
                ></i>
              </div>
              <h1>vs</h1>
              <div className="options">
                <i
                  className="fas fa-chevron-up"
                  onClick={this.handleUpRoad}
                ></i>
                <i
                  className="fas fa-chevron-down"
                  onClick={this.handleDownRoad}
                ></i>
              </div>
            </div>
          </div>
          <div
            className="road"
            style={{
              backgroundImage:
                "url(" + this.state.roadData[this.state.road].img + ")",
            }}
          >
            <h1>road</h1>
            <div className="road-team">
              <h2>{this.state.roadData[this.state.road].city}</h2>
              <h3>{this.state.roadData[this.state.road].name}</h3>
            </div>
          </div>
        </div>
      </section>
    );
  }
}

export default MainContent;
